@import
'_custom_bootstrap_vatiables.scss',
'../../bower_components/bootstrap/scss/_functions.scss',
'../../bower_components/bootstrap/scss/_variables.scss',
'../../bower_components/bootstrap/scss/_mixins.scss',
'_param',
'_sprite',
'_sprite_include.scss',
'_message_nav.scss',
'_header';
a.edit{
	&:hover{
		@include sprite($edit_hover);
	}
}

.field_map{
	width: 100%;
	position: fixed;
	top: 71px;
	left: 0;
	height: calc(100vh - 71px);
}
.roll {
    z-index: 100;
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 225px;
    height: 70px;
    color: #453625;
    font-size: 15px;
    padding: 18px 20px;
    text-align: center;
    background: url(../images/bg_roll.png) no-repeat;
}
.list_infrastructure{
	position: fixed;
	top: 204px;
	left: 5px;
	z-index: 10;
	a{
		display: block;
		width: 50px;
		height: 50px;
		position: relative;
		i{
			position: absolute;
			top: 50%;
			left: 50%;
			transform:translate(-50%,-50%);
		}
		&:hover{
			.ic_1{
				@include sprite($ic_1_hover);
				margin-top: -1px;
			}
			.ic_2{
				@include sprite($ic_2_hover);
				margin-left: -1px;
				margin-top: -1px;
			}
			.ic_3{
				@include sprite($ic_3_hover);

			}
			.ic_4{
				@include sprite($ic_4_hover);

			}
			.ic_5{
				@include sprite($ic_5_hover);
				margin-top: -1px;
			}
			.ic_6{
				@include sprite($ic_6_hover);
				margin-top: -1px;
			}
			.ic_7{
				@include sprite($ic_7_hover);

			}
			.ic_8{
				@include sprite($ic_8_hover);
				margin-left: -1px;
			}
			.ic_9{
				@include sprite($ic_9_hover);
			}
		}
	}
}
.user_box{
	width: 122px;
	height: 106px;
	position: fixed;
	top: 80px;
	left: 10px;
	.user_img{
		background: url(../images/bg_user_box.png) no-repeat;
		height: 100%;
		a{
			display: block;
			height: 100%;
			top: 10px;
			left: 17px;
			position: absolute;
			width: 87px;
			height: 87px;
			border-radius: 50%;
			background-size: cover !important;
			background-position: 50% 50%;
		}
	}
	.user_home{
		position: absolute;
		width: 48px;
		height: 45px;
		top: -5px;
		left: 2px;
	}
	.user_emblem{
		position: absolute;
		bottom: -9px;
		left: 5px;
		width: 52px;
		height: 45px;
	}
}
.user_home{
	a{
		&:hover{
			.tower{
				@include sprite($tower_hover);
				margin-top: 1px;
				margin-left: -1px;
			}
		}
	}
}
.user_emblem{
	@include sprite($holder_ic);
	a{
		display: block;
		height: 100%;
		border-radius: 50%;
		width: 36px;
		height: 36px;
		background-size: cover !important;
		background-position: 50% 50%;
		position: relative;
		top: 5px;
		left: 6px;
		opacity: .8;
		&:hover{
			opacity: 1;
		}
	}
}
.close_ico{
	width: 15px;
	height: 15px;
	display: inline-block;
	background: url('data:image/png;base64,') no-repeat;
	background-size: cover;
	opacity: .8;
	&:hover{
		opacity: 1;
	}
}
.alerts_block{
	position: fixed;
	right: 80px;
	top: 80px;
	width: 270px;
	max-height: calc(100vh - 155px);
	border-radius: 5px;
	padding:0 7px;
	.alert_item{
		margin-top: 10px;
		background: url(../images/bg_alert.png) repeat;
		padding: 15px 45px 15px 10px;
		border-radius: 5px;
		position: relative;
		color: #fff;
		text-shadow: 0px 3px 7px rgba(#000,.75);
		box-shadow: 0 0 2px 0px rgba(#000,.75);
		font-size: 14px;
		line-height: 17px;
		&:first-of-type{
			margin-top: 0;
		}
		.close_ico{
			position: absolute;
			top: 15px;
			right: 15px;
		}
		p{
			margin: 0;

		}
	}
}
.ps__rail-x,
.ps__rail-y{
	opacity: 1;
}
.ps--active-y{
	padding-right: 22px !important;
}
.ps__rail-y{
	width: 6px;
	background-color:transparent !important;
	right: 5px !important;
	opacity: 1 !important;
	&:after{
		content: '';
		width: 1px;
		height: calc(100% - 10px);
		display: block;
		background:#343434;
		font-size: 0;
		position: absolute;
		top: 5px;
		left: 1px;
		z-index: 1;
	}
	.ps__thumb-y{
		opacity: 1 !important;
		width: 6px !important;
		background-color: #626262 !important;
		// box-shadow: 0px 0px 3px 1px #757575;
		border-radius: 4px;
		z-index: 10;
	}
}
.test{
	position: fixed;
	width: 100px;
	height: 100px;
	background: red;
	top: 50%;
	left: 50%;
}